<template>
  <MarkerDefault :lngLat="lngLat" anchor="left">
    <div>
      <v-card
        class="main"
        max-width=550
        max-height=500
      >
        <v-card-title flat>
          <span>Информация по станции</span>
          <v-spacer></v-spacer>
          <v-btn @click="closePanel()" icon>
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-tabs
          v-model="tab"
          grow
          slider-color="#FF7141"
        >
          <v-tab>
            Планирование
          </v-tab>
          <v-tab>
            Поезда
          </v-tab>
          <v-tab>
            Вагоны
          </v-tab>
        </v-tabs>
        <v-tabs-items
          v-model="tab"
        >
          <v-tab-item>
            <table width="100%" height="100%">
              <tr >
                <td align="center" colspan="3">
                  <p>Сменно-суточное планирование<br>
                  {{stationName}}</p>
                </td>
              </tr>
              <tr>
                <td class="td-common" align="center">
                  ПЧ: {{responseData.train_all_day_arriv_even}} / {{responseData.plan_arriv_even}}
                </td>
                <td class="td-common" align="center">
                  ОЧ: {{responseData.train_all_day_dep_even}} / {{responseData.plan_dep_even}}
                </td>
              </tr>
              <tr>
                <td class="td-common" align="center">
                  ПН: {{responseData.train_all_day_arriv_odd}} / {{responseData.plan_arriv_odd}}
                </td>
                <td class="td-common" align="center">
                  ОН: {{responseData.train_all_day_dep_odd}} / {{responseData.plan_dep_odd}}
                </td>
              </tr>
            </table>
          </v-tab-item>
          <v-tab-item>
            2
          </v-tab-item>
          <v-tab-item>
            3
          </v-tab-item>
        </v-tabs-items>
      <br>
      <v-divider></v-divider>
      <div class="font-weight-light">
        <span class="mr-3"><span class="legend-key green-key"/> Направление 1</span>
      </div>
      <div class="font-weight-light">
        <span class="mr-3"><span class="legend-key red-key"/> Направление 2</span>
      </div>
      </v-card>
    </div>
  </MarkerDefault>
</template>

<script>
import MarkerDefault from '@/components/MarkerDefault'

export default {
  name: 'MarkerStation',
  components: {
    MarkerDefault
  },
  data: () => ({
    tab: null
  }),
  props: {
    value: {
      type: Boolean,
      required: true
    },
    stationName: {
      type: String,
      required: true
    },
    lngLat: {
      type: Array,
      required: true
    },
    responseData: {
      type: Object
    }
  },
  created () {
    // this.donutCreate()
  },
  methods: {
    closePanel () {
      this.$emit('input', false)
    }
  }
}
</script>

<style scoped lang="sass">
.main
  transform: translateX(50px)
.legend-key
  display: inline-block
  border-radius: 20%
  width: 10px
  height: 10px
  margin-right: 5px
  margin-left: 5px
.red-key
  background-color: red
.green-key
  background-color: green

.td-common
  width: 50%
  height: 50%
</style>
