<template>
  <div>
    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      :center="center"
      :zoom.sync="zoom"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-right"/>
    </MglMap>
    <div v-if="mapLoaded">
      <div v-if="roadLayerSwitch">
        <div v-for="(roadMarker, name) of roadMarkers" :key="name">
          <MarkerRoad v-if="zoom >= 3.8 && zoom < 8"
            :roadName="roadMarker.roadName"
            :lngLat="roadMarker.lngLat"
            :shortName="roadMarker.shortName"
            :responseData="roadMarker.responseData"
          />
        </div>
      </div>

      <div v-if="displayMarkerStation">
        <MarkerStation
          v-model="displayMarkerStation"
          :stationName="stationInfo.stationName"
          :lngLat="stationInfo.lngLat"
          :responseData="stationInfo.responseData"
        />
      </div>

      <div v-if="roadLayerSwitch">
        <PanelRoad :road="panelRoad[Object.keys(panelRoad)[0]]"/>
      </div>

      <DialogError v-model="errorLoadingData"/>

      <v-card
        class="overlay"
        width="350"
        rounded
        elevation="3"
      >
        <v-text-field
          v-model="stationSearch"
          hide-details
          prepend-inner-icon="mdi-magnify"
          solo
          label="Найти станцию"
        ></v-text-field>
        <v-list-item
          two-line
          v-for="(station, i) in filteredStations"
          :key="i"
          @click="goToStation(station.esr, station.lngLat.coordinates)"
        >
          <v-list-item-content>
            <v-list-item-title>{{ station.name }}</v-list-item-title>
            <!-- <v-list-item-subtitle>{{ station.railwayName }}</v-list-item-subtitle> -->
          </v-list-item-content>
        </v-list-item>
      </v-card>

      <v-btn
      class='floatingButton'
      elevation="3"
      fab
      >
        <v-icon @click='layersFilter = !layersFilter'>mdi-cog</v-icon>
      </v-btn>

      <v-btn
      class='errorButton'
      elevation="3"
      fab
      >
        <v-icon @click='openDialogError()'>mdi-reload</v-icon>
      </v-btn>
    </div> <!--if maploaded-->
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Загрузка
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-navigation-drawer
      v-model="layersFilter"
      class="layersFilter"
      absolute
      temporary
    >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title><b>Настройка слоёв</b></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list-item>
        <v-list-item-content>
          <v-switch
            v-model="streamLayerSwitch"
            class='filterSwitch'
            label="Вагоно-потоки"
            @change="switchStreams()"
          ></v-switch>
          <v-switch
            v-model="roadLayerSwitch"
            class='filterSwitch'
            label="Показатели по дороге"
          ></v-switch>
        </v-list-item-content>
      </v-list-item>
    </v-navigation-drawer>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
// import Mapbox from 'mapbox-gl'
import { MglMap, MglNavigationControl } from 'vue-mapbox'
import mapStyle from '@/assets/map-style.json'
import styleVariables from '@/css/variables.sass'

import planingStationsJSON from '@/assets/planingStationsJSON.json'
import stationsJSON from '@/assets/all_stations.json'
import staticDataFomTKP from '@/assets/staticDataFromTKP.json'

import MarkerRoad from '@/components/MarkerRoad'
import MarkerStation from '@/components/MarkerStation'
import PanelRoad from '@/components/PanelRoad'

import DialogError from '@/components/DialogError'

export default {
  name: 'Home',
  components: {
    MglMap,
    MglNavigationControl,
    MarkerRoad,
    MarkerStation,
    PanelRoad,
    DialogError
  },

  data: () => ({
    accessToken: 'pk.eyJ1IjoiY2hpZ2EiLCJhIjoiY2tqYnRyN2NzN3B4ZTJxcWpvdmVldGdmYSJ9.mTIH5Hby4yDyU-LLFOY0Fw',
    mapStyle: mapStyle,
    zoom: 3.4,
    mapLoaded: false,
    center: [83.214, 60.703],
    loading: false,
    stationSearch: null,
    dataFromTKP: {},
    railway: {},
    streamLines: [],
    layersFilter: null,
    streamLayerSwitch: true,
    roadLayerSwitch: true,
    errorLoadingData: false,
    roadMarkers: [],
    stationInfo: {},
    allStations: {},
    allStationsMap: {},
    panelRoad: {},
    displayMarkerStation: false
  }),

  methods: {
    onMapLoaded: function (event) {
      this.$store.map = event.map
      this.railway = planingStationsJSON
      this.allStations = stationsJSON
      this.createRailAreas()
      this.createStreamLines()
      this.createStations()
      this.createPlaningStations()
      this.loadData()
      const map = this.$store.map
      map.doubleClickZoom.disable()
      // click and hover on station
      map.on('click', 'planing-station-point', (event) => {
        const esr = event.features[0].properties.esr
        const lngLat = event.features[0].geometry.coordinates
        this.goToStation(esr, lngLat)
      })
      map.on('mouseenter', 'planing-station-point', function () {
        map.getCanvas().style.cursor = 'pointer'
      })
      map.on('mouseleave', 'planing-station-point', function () {
        map.getCanvas().style.cursor = ''
      })
      map.on('moveend', () => {
        if (this.zoom >= 8) {
          const roadFeatures = map.queryRenderedFeatures({ layers: ['rail-polygons'] })
          const reqRoadCode = roadFeatures[0].properties.code
          for (const road of this.roadMarkers) {
            if (road.roadCode === reqRoadCode) {
              if (!Object.prototype.hasOwnProperty.call(this.panelRoad, reqRoadCode)) {
                this.panelRoad = {}
                this.panelRoad[reqRoadCode] = road
              }
            }
          }
        } else {
          this.stationMarker = {}
          this.panelRoad = {}
          this.displayMarkerStation = false
        }
      })
    },

    async loadData () {
      console.log('Starting data load...')
      this.loading = true
      await stationsJSON.features.forEach(feature => {
        this.allStationsMap[feature.properties.esr] = feature.properties
      })
      this.dataFromTKP = staticDataFomTKP
      // await this.getGlobalData()
      await this.getMarkerRoadData()
      await this.getMarkerStationData()
      this.mapLoaded = true
      this.loading = false
      console.log('Data load comlete!')
    },

    async getGlobalData () {
      try {
        const { requestData } = await axios.get('ajax2.php', {
          params: {
            module: 'TKP',
            method: 'get_gis_data'
          }
        })
        console.log('Requested data: ')
        console.log(requestData)
        this.dataFromTKP = requestData
      } catch (e) {
        console.log('Can\'t load data from TKP. loadData(): ')
        console.log(e)
        this.openDialogError()
      }

      if (this.dataFromTKP === undefined) {
        this.dataFromTKP = staticDataFomTKP
      }
    },

    async getMarkerRoadData () {
      const roadData = this.dataFromTKP.data.tkp_dimensions_cutr
      this.railway.roads.map(road => {
        if (road.code in roadData) {
          road.responseData = roadData[road.code]
        }
      })
      this.updateRoadMarkers()
    },

    updateRoadMarkers () {
      const coordinatesMap = {
        88: [93.449533, 55.668329],
        92: [108.191104, 53.626679],
        94: [119.502052, 55.197234],
        96: [138.168995, 48.295739]
      }
      for (const road of this.railway.roads) {
        this.roadMarkers.push({
          roadCode: road.code,
          roadName: road.name,
          lngLat: coordinatesMap[road.code],
          shortName: road.shortName,
          responseData: road.responseData
        })
      }
    },

    async getMarkerStationData () {
      this.dataFromTKP.data.station.forEach(item => {
        this.allStationsMap[item.esr].responseData = item
      })
    },

    goToStation (esr, lngLat) {
      this.displayMarkerStation = false
      this.stationInfo = {}
      this.$store.map.flyTo({
        center: lngLat,
        zoom: 10,
        bearing: 0,
        speed: 2,
        essential: true
      })
      this.stationInfo = {
        stationName: this.allStationsMap[esr].name,
        lngLat: lngLat,
        responseData: this.allStationsMap[esr].responseData
      }
      this.stationSearch = ''
      this.openMarkerStation()
    },

    openMarkerStation () {
      this.displayMarkerStation = true
    },

    switchStreams () {
      const map = this.$store.map
      if (this.streamLayerSwitch) {
        map.setLayoutProperty('stream', 'visibility', 'visible')
      } else {
        map.setLayoutProperty('stream', 'visibility', 'none')
      }
    },

    openDialogError () {
      this.errorLoadingData = true
    },

    createRailAreas () {
      this.$store.map.addSource('rail-areas', {
        type: 'geojson',
        data: require('@/assets/railAreas.json')
      })
      this.$store.map.addLayer({
        id: 'rail-polygons',
        type: 'fill',
        source: 'rail-areas',
        layout: {},
        paint: {
          'fill-color': [
            'match', ['get', 'code'],
            [10, 24, 61, 83, 96], styleVariables.railArea1,
            [28, 41, 63, 88], styleVariables.railArea2,
            [17, 51, 80, 92], styleVariables.railArea3,
            [1, 58, 76, 94], styleVariables.railArea4,
            /* other */ 'grey'
          ],
          'fill-opacity': [
            'interpolate', ['linear'], ['zoom'],
            0, 0.7,
            2, 0.20,
            5, 0.20,
            6, 0
          ]
        }
      })
      this.$store.map.addLayer({
        id: 'rail-borders',
        type: 'line',
        source: 'rail-areas',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        minzoom: 5.5,
        paint: {
          'line-color': styleVariables.railBorder,
          'line-width': 2,
          'line-opacity': [
            'interpolate', ['linear'], ['zoom'],
            5.5, 0,
            6.5, 0.6
          ]
        }
      })
    },

    createPlaningStations () {
      const psJson = {
        type: 'FeatureCollection',
        features: []
      }
      this.railway.roads.forEach(road => {
        road.stations.forEach(station => {
          psJson.features.push({
            type: 'Feature',
            properties: {
              name: station.name,
              esr: station.esr,
              roadCode: road.code
            },
            geometry: {
              type: 'Point',
              coordinates: station.lngLat
            }
          })
        })
      })
      this.$store.map.addSource('planingStations', {
        type: 'geojson',
        data: psJson
      })
      this.$store.map.addLayer({
        id: 'planing-station-point',
        type: 'circle',
        source: 'planingStations',
        minzoom: 3.8,
        paint: {
          'circle-stroke-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ],
          'circle-radius': {
            stops: [
              [3, 5],
              [6, 7],
              [12, 9]
            ]
          },
          'circle-color': '#3333ff',
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ]
        }
      })
      this.$store.map.addLayer({
        id: 'planing-stations-labels',
        type: 'symbol',
        source: 'planingStations',
        minzoom: 5,
        layout: {
          'text-field': ['get', 'name'],
          'text-font': [
            'Metropolis Light',
            'Noto Sans Regular'
          ],
          'text-anchor': 'bottom',
          'text-offset': [0, -1],
          'text-size': 15
        },
        paint: {
          'text-halo-width': 1,
          'text-color': '#000000'
        }
      })
    },

    createStations () {
      this.$store.map.addSource('stations', {
        type: 'geojson',
        data: require('@/assets/all_stations.json')
      })

      this.$store.map.addLayer({
        id: 'station-point',
        type: 'circle',
        source: 'stations',
        minzoom: 7,
        paint: {
          'circle-stroke-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            7, 0,
            8, 1
          ],
          'circle-radius': {
            stops: [
              [3, 2.5],
              [6, 5],
              [12, 7]
            ]
          },
          'circle-color': 'black',
          // 'circle-color': [
          //   // // 'green'
          //   // 'interpolate',
          //   // ['linear'],
          //   // ['get', 'load'],
          //   // -1, 'grey',
          //   // 0, 'green',
          //   // 0.5, 'yellow',
          //   // 1, 'red',
          //   // 1.01, '#800000'
          // ],
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            7, 0,
            8, 1
          ]
        }
      })

      this.$store.map.addLayer({
        id: 'stations-labels',
        type: 'symbol',
        source: 'stations',
        minzoom: 7,
        layout: {
          'text-field': ['get', 'name'],
          'text-font': [
            'Metropolis Light',
            'Noto Sans Regular'
          ],
          'text-anchor': 'bottom',
          'text-offset': [0, -1],
          'text-size': 15
        },
        paint: {
          'text-halo-width': 1,
          'text-color': '#646464'
        }
      })
    },
    createStreamLines () {
      this.$store.map.addSource('streams', {
        type: 'geojson',
        data: require('@/assets/all_peregon.json')
      })

      this.$store.map.addLayer({
        id: 'stream',
        type: 'line',
        source: 'streams',
        minzoom: 3.8,
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        paint: {
          'line-color': '#408000'
        }
      })

      // this.$store.map.addLayer({
      //   id: 'streamText',
      //   type: 'symbol',
      //   source: 'streams',
      //   minzoom: 3.8,
      //   layout: {
      //     'symbol-placement': 'line',
      //     'text-font': [
      //       'Metropolis Light',
      //       'Noto Sans Regular'
      //     ],
      //     'text-field': '{width}',
      //     'text-size': 12
      //   },
      //   paint: {}
      // })
    }
  },
  computed: {
    filteredStations () {
      if (!this.stationSearch) return []
      const a = this.allStations.features
        .map(feature => {
          const lngLat = feature.geometry
          return {
            ...feature.properties,
            lngLat
          }
        })
        // take all stations
        // .reduce((accumulator, feature) => accumulator.concat(feature.stations), [])
        // filter by user input
        .filter(station => station.name.toLowerCase().indexOf(this.stationSearch.toLowerCase()) === 0)
        // sort by alphabet
        .sort((a, b) => a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1)
        // slice first 10 elements
        .slice(0, 10)
      // console.log(a)
      return a
    }
  },
  watch: {
    // railway: {
    //   handler: function () {
    //     const roadsGeoJSON = this.getRoadsGeoJSON()
    //     // console.log(roadsGeoJSON)
    //     this.$store.map.getSource('stations').setData(roadsGeoJSON)
    //     // this.updateRoadMarkers()
    //   },
    //   deep: true
    // }
  }
}
</script>

<style scoped lang="sass">
.map
  position: absolute
  top: 0
  bottom: 0
  width: 100%

.overlay
  position: absolute
  user-select: none
  margin: 10px

.floatingButton
  box-shadow: 0 0 0 2px rgba(0,0,0,.1) !important
  position: absolute
  left: 0
  top: 0
  margin: 10px 370px
  border-radius: 4px !important
  width: 29px !important
  height: 48px !important

.errorButton
  box-shadow: 0 0 0 2px rgba(0,0,0,.1) !important
  position: absolute
  right: 0
  top: 0
  margin: 110px 10px
  border-radius: 4px !important
  width: 29px !important
  height: 29px !important

.parent .filterSwitch
  margin-left: 10px !important
  color: #FFFFFF !important

.layersFilter
  background-color: #00194D !important
  position: absolute
  color: #FFFFFF !important
</style>
